import React, {Component} from 'react';
import './TagsView.css';

export default class Tags extends Component {

    constructor(props) {
        super(props);
        this.tags=[];
        this.createTags(props);
    }

    render() {
        let tags = [];
        for(let tag of this.tags) {
            tags.push(
            <span className="tag" key={`tag_${tags.length}`}>{tag}
                <button>X</button>
            </span>);
        }
        return <div className="tags">{tags}</div>
    }

    createTags = (props) => {
        if (props.fileName) {
            this.tags = [];
            this.newTags = props.fileName.split(/[\s-]/);

            for (let tag of this.newTags) {
                if (!this.tags.includes(tag)) {
                    this.tags.push(tag);
                }
            }
        }
    }

    componentWillReceiveProps = (newProps) => {
        this.createTags(newProps);
    }
}


