import React, { Component } from 'react';
import './FileNameUpdater.css';
import service from './service';
import TagsView from './tag/TagsView';
import AddTag from './tag/AddTag';

export default class FileNameUpdater extends Component {

    constructor(props) {
        super(props);
        this.state = {
            lastName: props.name,
            name: props.name
        }
    }

    componentWillReceiveProps = (newProps) => {
        this.setState((prevState) => {
            return Object.assign({}, prevState, {
                name: newProps.name,
                lastName: newProps.name
            });
        });
    }

    render = () => {
        let uptoDateClass = this.state.lastName === this.state.name ? 'update--disabled' : 'update--enabled';

        return <div className="file-name-updater" id={this.props.id}>
            <dl>
            <dt><a href={this.props.webContentLink} alt="grab the content">{this.props.fullFileExtension}</a></dt>
            <dd>
                <a href={this.props.webViewLink} target="_blank">
                    <img src={this.props.thumbnailLink} alt="thumbnail"></img><br/>
                    <img src={this.props.iconLink} alt={this.props.description || 'icon'} />
                    {this.state.name}
                </a>
            </dd>
            <dt>Tags</dt>
            <dd>
                <AddTag onAdd={this.onAddTag}/>
                <TagsView fileName={this.state.name} />
            </dd>
            <dt>Actions</dt>
            <dd>
                <button onClick={this.splitName}>Split</button>
                <button onClick={this.saveName} className={uptoDateClass}>Save</button>
            </dd>
            </dl>
        </div>
    }

    onAddTag = (newTag) => {
        this.setState( prevState => {
            return {
                lastName: prevState.name,
                name: `${prevState.name} ${newTag}`
            }
        })
    }

    splitName = () => {
        this.setState((prevState) => {

            return {
                lastName: prevState.name,
                name: this.sanitizeFileName(prevState.name)
            }
        });
    }

    saveName = () => {
        service.updateFileName(this.props.id, this.state.name).then((resp) => {
            this.setState(prevState => {
                return {
                    lastName: resp.result.name,
                    name: resp.result.name
                }
            });

            if (typeof this.props.onFileNameChanged === 'function') {
                this.props.onFileNameChanged(resp.result.id, resp.result.name);
            }
        });
    }

    sanitizeFileName = (name) => {
        let newName = name.replace(/[_.]/g, ' ').toLowerCase();
            //.replace(new RegExp(this.props.fullFileExtension,'g'),'');

        return newName;
    }
}


