import React, { Component } from 'react';
import './File.css';

import TagsView from './tag/TagsView';

export default class File extends Component {

    render = () => {

        let owners = this.props.owners.map(owner => <img key={owner.emailAddress} className="owner__picture" src={owner.photoLink} alt={owner.displayName} />);

        return <li className="file" onMouseEnter={this.onClick}>
            {this.modifiers()}
            {owners}
            <a href={this.props.webViewLink} target="_blank"><img src={this.props.iconLink} alt={this.props.description || 'icon'} /></a>
            <TagsView fileName={this.props.name}/>
        </li>;
    }

    onClick = () => {
        this.props.onSelected(this.props);
    }

    modifiers() {
        return <span className="file__modifiers">
            {this.props.shared ? <i className="icon-users" /> : ''}
            {this.props.starred ? <i className="icon-star" /> : <i className="icon-star-empty" />}
            {this.props.spaces.includes('photos') ? <i className="icon-camera" /> : ''}
            </span>;
    }
}


