import React, { Component } from 'react';
import File from './File';
import './ListFiles.css';
import Error from './Error';
import service from './service';
import FileNameUpdater from './FileNameUpdater';

export default class ListFiles extends Component {
    constructor(props) {
        super(props);
        this.state = {
            files: []
        }
    }

    render() {
        const files = this.state.files.map((fileMetaData) => <File key={fileMetaData.id} {...fileMetaData} onSelected={this.onFileSelected} />);
        const fileNameUpdater = this.state.selectedFile ? <FileNameUpdater
            {...this.state.selectedFile}
            onFileNameChanged={this.onFileNameChanged} /> : null;
        return (
            <div className="file-list" onScroll={this.onScroll}>
                <div className="file-list--pane-left">
                    <Error error={this.state.error} />
                    <ol>{files}</ol>
                </div>
                <div className="file-list--pane-right">
                    {fileNameUpdater}
                </div>
            </div>)
    }

    onFileNameChanged = (id, newFileName) => {
        this.setState(prevState => {
            var newState = prevState;

            for (let i = 0; i < newState.files.length; i++) {
                const file = newState.files[i];
                if (file.id === id) {
                    file.name = newFileName;
                    break;
                }
            }

            return newState;
        });
    }

    onFileSelected = (file) => {
        this.setState((prevState) => {
            return Object.assign({}, prevState, {selectedFile: file});
        });
    }

    listFiles = (params) => {
        service.fetchFileList(params, this.props.searchParams).then(this.newFilesInResponse).catch(this.errorHandler);
    }

    nextPage = () => {
        service.fetchFileList({ pageToken: this.state.nextPageToken }, this.props.searchParams).then(this.newFilesInResponse).catch(this.errorHandler);
    }

    errorHandler = (err) => { this.setState({ error: err.result.error }) }

    newFilesInResponse = (resp) => {
        this.setState((prevState) => {
            let newState = Object.assign({}, prevState);
            newState.files = newState.files.concat(resp.result.files);
            newState.nextPageToken = resp.result.nextPageToken;
            newState.error = '';

            return newState;
        });
    }

    componentWillReceiveProps = (newProps) => {
        this.setState({ files: [] });
        this.listFiles({ q: newProps.searchParams });
    }

    componentDidMount = () => {
        this.listFiles();
        window.addEventListener('scroll', this.onScroll);
    }

    onScroll = (ev) => {
        const ele = document.querySelector('.file-list');
        if (this.state.nextPageToken && !this.onGoingRequestPromise) {
            if (window.scrollY > (ele.scrollHeight - window.innerHeight)) {
                this.nextPage();
            }
        }
    }
}



