import React, {Component} from 'react';
import './AddTag.css';

export default class AddTag extends Component {

    render() {
        return <div className="addTag"><input type="text" onKeyUp={this.onKeyUp}/></div>
    }

    componentDidMount = () => {
        this.input = document.querySelector('.addTag input');
    }

    onKeyUp = (key) => {
        if(key.key === 'Enter') {
            this.props.onAdd(this.input.value);
            this.input.value='';
        }
        return true;
    }
}


